import uniPopup from "@/components/uni-popup/uni-popupPayResults.vue"
import route from "@/common/public.js"
import {
	mapGetters,
	mapActions
} from 'vuex';


export default {
	components: {uniPopup},
	computed: {
		...mapGetters(['statusBarHeight'])
	},
	data() {
		return {
			height:'',
			Signboard:'',
			Signguid:'',
			code:'',//验证码
			trade_pwd: '',
			old_pwd:'',
			new_pwd:'',
			comfirm_pwd:'',
			// focus: true,
			show: false,
			numarr: [],
			cztime:0,
			numlength: "",
			
			telephone:'',//获取当前主账户注册的手机号
			getCodeText:'获取验证码',
			getCodeBtnColor:"#ff0000",
			getCodeisWaiting:false,
			showPopup:false,
			boardlists: [{
					id: 1,
					con: 1,
					checked: false
				}, {
					id: 2,
					con: 2,
					checked: false
				}, {
					id: 3,
					con: 3,
					checked: false
				}, {
					id: 4,
					con: 4,
					checked: false
				}, {
					id: 5,
					con: 5,
					checked: false
				}, {
					id: 6,
					con: 6,
					checked: false
				}, {
					id: 7,
					con: 7,
					checked: false
				}, {
					id: 8,
					con: 8,
					checked: false
				}, {
					id: 9,
					con: 9,
					checked: false
				}, {
					id: 10,
					con: "",
					checked: false
				}, {
					id: 11,
					con: 0,
					checked: false
				},
				{
					id: 12,
					con: "",
					checked: false
				},
			],
			tips:[
				{msg:'请输入旧支付密码'},
				{msg:'请输入新支付密码'},
				{msg:'请再次输入新支付密码'},
				{msg:'请输入新支付密码'},
			]
		};
	},
	computed: {
		len: function() {
			if (this.trade_pwd.length == 6) {
				// 第一次进来可以点击的

			}
			return this.trade_pwd.length
		}
	},
	onLoad(e) {
		try {
		    this.height = this.winHeight
		} catch (e) {
		    // error
		}
		if(e.cztime){
			//判断是否从找回支付密码页面过来，是就把cztime改为3
			this.cztime = 3;
			this.showPopup = true;
			uni.setNavigationBarTitle({
				title:'找回支付密码'
			});
		}
		var jsonList=uni.getStorageSync("jsonList");
		var data = JSON.parse(jsonList); //JSON字符串转对象
		this.Signboard = data.Ident_Signboard;
		this.Signguid = data.Ident_Signguid;
		uni.request({
			url:route.variable+'/mobile/Subaccount/getMain', //获取验证时调用的接口
			method:'POST',
			data:{
				Ident_Signboard: this.Signboard,
				Ident_Signguid: this.Signguid,
			},
			success: (result) => {
				console.log(result.data.telephone);
				if(result.data.status==0){
					this.telephone=result.data.telephone;
				}else{
					uni.showToast({title: '获取原手机号失败！，'+result.data.message,icon:"none"});
				}
			},
			fail:(e)=>{
				uni.showToast({title: '获取原手机号失败'+'错误码201',icon:"none"});
				console.log("fail: "+'错误码201');
			}
		})
	},
	methods: {
		Timer(){},
		changeStatus(flag){
			switch (flag){
				case 0:
					this.getCodeText = "获取验证码";
					this.getCodeisWaiting = false;
					this.getCodeBtnColor = "#ff0000";
					
					break;
				case 1:
					this.getCodeText = "发送中...";
					this.getCodeisWaiting = true;
					this.getCodeBtnColor = "rgba(255,0,0,0.5)";
					break;
				case 2:
					this.getCodeText = "重新获取(60)";
					this.getCodeisWaiting = false;
					this.getCodeBtnColor = "#ff0000";
					break;
				default:
					break;
			}
		},
		getCode(){
			if(this.getCodeisWaiting){
				return ;
			}
			this.changeStatus(1);
			//示例用定时器模拟请求效果
			setTimeout(()=>{
				uni.request({
					url:route.variable+'/mobile/security/verCode', //获取验证时调用的接口
					method:'POST',
					data:{
						telephone:this.telephone
					},
					success: (result) => {
						console.log(result);
						if(result.data.status==0){
							uni.showToast({title: '验证码已发送',icon:"none"});
						}else{
							uni.showToast({title: '获取验证码失败，'+result.data.message,icon:"none"});
							this.changeStatus(0);
						}
					},
					fail:(res)=>{
						uni.showToast({title: '获取验证码失败'+'错误码201',icon:"none"});
						console.log("fail: "+JSON.stringify(e));
						this.changeStatus(0);
					}
				})
				this.setTimer();
			},1000)
			
			
		},
		setTimer(){
			let holdTime = 60;
			this.getCodeText = "重新获取(60)"
			this.Timer = setInterval(()=>{
				if(holdTime<=0){
					this.getCodeisWaiting = false;
					this.getCodeBtnColor = "#ff0000";
					this.getCodeText = "获取验证码"
					clearInterval(this.Timer);
					return ;
				}
				this.getCodeText = "重新获取("+holdTime+")"
				holdTime--;
				
			},1000)
		},
		codeBtn(){
			//判断验证码是否为空
			if(this.code==''){ 
				uni.showToast({title: '请输入验证码',icon:"none"});
				return false; 
			}
			
			uni.request({
				url:route.variable+'/mobile/security/validation',
				method:'POST',
				data:{
					Ident_Signboard: this.Signboard,
					Ident_Signguid: this.Signguid,
					telephone:this.telephone,
					vercode:this.code
				},
				success: (res) =>{
					console.log(res);
					if(res.data.status==0){
						this.showPopup =false
					}else{
						uni.showToast({title:'验证不通过，'+res.data.message,icon:'none'})
					}
				},
				fail:(e)=>{
					uni.hideLoading()
					uni.showToast({title: '修改支付密码失败'+'错误码201',icon:"none"});
					console.log("fail: "+JSON.stringify(e));
				}
			})
		},
		writepwd(num) {
			if (num == 12) {
				this.close();
				return;
			}
			if (num == 10) {
				return;
			}
			if (num == 11) {
				num = 0;
				this.boardlists[10].checked = true;
			} else {
				this.boardlists[num - 1].checked = true;
			}
			this.numarr.push(num);
			if (this.numarr.length > 6) {
				return
			}
			var that = this;
			setTimeout(function() {
				for (var i = 0; i < that.boardlists.length; i++) {
					that.boardlists[i].checked = false;
				}
			}, 200)
			this.trade_pwd = this.numarr.join("");


		},
		//密码框
		back() {
			uni.navigateBack();
		},
		//回退方法
		close() {
			this.numarr.pop();
			if (this.trade_pwd.length > 0) {
				this.trade_pwd = this.trade_pwd.substring(0, this.trade_pwd.length - 1);
			}
		},
		blur1() {
			this.show = false
		},
		focus1() {

			this.show = true
		},
		reset(){
			this.trade_pwd = '';
			this.show = true
			this.numarr = [],
			this.numlength = ""
		},
		// 确认执行的方法
		setpwd() {
			if (this.trade_pwd.length < 6) {
				uni.showToast({title: '密码长度不能少于6位',icon:"none",duration:2000});
				return;
			}
			switch (this.cztime){
				case 0:
					this.old_pwd = this.trade_pwd;
					uni.request({
						url:route.variable+'/mobile/security/check_pass',
						method:'GET',
						data:{
							Ident_Signboard: this.Signboard,
							Ident_Signguid: this.Signguid,
							oldpay:this.old_pwd,
						},
						success: (res) => {
							uni.hideLoading();
							if (route.publicIf(res.data.status) == false){
								return false;
							}
							if(res.data.status==0){
								// uni.showToast({title: '支付密码修改成功，返回首页',icon:"none",duration:4000});
								++this.cztime;
								this.reset()
							}else{
								uni.showToast({title: '原支付密码不正确,请重新输入'+res.data.message,icon:"none"});
								this.reset();
							}
						},
						fail:(res)=>{
							uni.hideLoading()
							uni.showToast({title: '原支付密码验证失败,请重新输入'+'错误码201',icon:"none"});
							console.log("fail: "+JSON.stringify(e));
							this.reset();
						}	
					})	
					
					break;
				case 1:
					this.new_pwd = this.trade_pwd;
					this.reset()
					if(this.new_pwd==this.old_pwd){
						uni.showToast({title: '不能和旧支付密码一致，请重新输入',icon:"none"});
						return false; 
					}
					++this.cztime;
					break;
				case 2:
					this.comfirm_pwd = this.trade_pwd;
					if(this.new_pwd != this.comfirm_pwd){
						uni.showToast({title: '请确认两次输入的新支付密码一致',icon:"none"});
						this.reset()
						return false;
					}
					uni.showLoading({
						title: '修改中...'
					})
					uni.request({
						url:route.variable+'/mobile/Modify/updatePayPwd',
						method:'GET',
						data:{
							Ident_Signboard: this.Signboard,
							Ident_Signguid: this.Signguid,
							oldpay:this.old_pwd,
							newpay:this.new_pwd,
						},
						success: (res) => {
							if (route.publicIf(res.data.status) == false){
								return false;
							}
							if(res.data.status==0){
								uni.showToast({title: '支付密码修改成功，返回首页',icon:"none",duration:4000});
								setTimeout(function(){
									uni.redirectTo({
										url: '/pages/index/index'
									});
								},4000)
							}else{
								uni.hideLoading();
								uni.showToast({title: '支付密码修改失败,'+res.data.message,icon:"none"});
								this.reset();
							}
						},
						fail:(res)=>{
							uni.hideLoading()
							uni.showToast({title: '支付密码修改失败'+'错误码201',icon:"none"});
							console.log("fail: "+JSON.stringify(e));
							this.reset();
						}	
					})	
					break;
				case 3:
					this.new_pwd = this.trade_pwd;
					uni.request({
						url:route.variable+'/mobile/security/resetpayment',
						method:'POST',
						data:{
							Ident_Signboard: this.Signboard,
							Ident_Signguid: this.Signguid,
							password:this.new_pwd
						},
						success: (res) =>{
							console.log(res);
							if(res.data.status==0){
								uni.showToast({title:'重设支付密码成功，返回首页',icon:'none',duration:4000})
								setTimeout(function(){
									uni.redirectTo({
										url: '/pages/index/index'
									});
								},4000)
							}else{
								uni.showToast({title:'重设支付密码失败，请重新输入密码',icon:'none'})
								this.reset();
							}
						},
						fail:(e)=>{
							uni.hideLoading()
							uni.showToast({title:'重设支付密码失败'+'错误码201',icon:'none'})
							console.log("fail: "+JSON.stringify(e));
							this.reset();
						}
					})
					break;
				default:
					break;
			}
			console.log(this.old_pwd);
			console.log(this.new_pwd);
			console.log(this.comfirm_pwd);
			
		},

	}

};
